

.intro__container {
    background-color: var(--backgrounddark);
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.intro__contents {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0%;
    width: 75%;
    color: var(--white);
    font-family: 'Reem Kufi Ink', sans-serif;
}

.intro__text {
    font-size: 50px;
    color: var(--textdark);
}

.intro__text:hover {
    color: red;
    transition: all 0.75s ease-out;
}

.intro__name {
    font-size: 100px;
}

.intro__name:hover {
    color: red;
    transition: all 0.75s ease-out;
}